import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../services/client.service';
@Component({
  selector: 'app-clients',
  templateUrl: './clients.component.html',
  styleUrls: ['./clients.component.css']
})
export class ClientsComponent implements OnInit {

  constructor(private clientService: ClientService) { }
  clients;
  first_name;
  last_name;
  email;
  phone;
  isEdit;
  _id;
  ngOnInit() {
    console.log(this.clientService.getClients());
    this.clientService.getClients().subscribe(clients => {
      console.log(clients);
      this.clients = clients;
    });
    this.isEdit = false;
  }
  onAddSubmit() {
    console.log("onAddSubmit");
    let newClient = {
      first_name: this.first_name,
      last_name: this.last_name,
      email: this.email,
      phone: this.phone,
    }
    console.log(newClient);
    if (newClient.first_name == undefined || newClient.last_name == undefined || newClient.email == undefined || newClient.phone == undefined) {
      return;
    } else {
      this.clientService.saveClient(newClient).subscribe(client => {
        this.first_name = '';
        this.last_name = '';
        this.email = '';
        this.phone = '';
        window.location.href = "/"
      });
    }
  }

  onEditClick(client) {
    this.isEdit = !this.isEdit;
    this.first_name = client.first_name;
    this.last_name = client.last_name;
    this.email = client.email;
    this.phone = client.phone;
    this._id = client._id;
    console.log(this._id);
  }

  onEditSubmit() {
    console.log();
    let updatedClient = {
      first_name: this.first_name,
      last_name: this.last_name,
      email: this.email,
      phone: this.phone,
      _id: this._id
    }
    if (this.first_name == undefined || this.last_name == undefined || this.email == undefined || this.phone == undefined) {
      return;
    }
    this.clientService.updateClient(updatedClient, updatedClient._id).subscribe(client => {
      this.first_name = '';
      this.last_name = '';
      this.email = '';
      this.phone = '';
      window.location.href = '/'
    });

  }

  onDeleteClick(id) {

    this.clientService.deleteClient(id).subscribe(client => {
      console.log("Here is the client : ", client)
      for (let i = 0; i < this.clients.length; i++) {
        if (id == this.clients[i]._id) {
          this.clients.splice(i, 1);
        }
      }
    });
  }

}
